<template>
  <div class="container" @scroll="add">
    <div class="header">
      <div class="header_header">
        <h2>游戏专区</h2>
        <h5>热门游戏资源，近三千款游戏大作</h5>
        <h5>提取码无：表示无需提取码</h5>
        <h5>注意：部分游戏要求游戏安装目录为英文，Windows用户名为英文</h5>
      </div>
    </div>
    <div class="header_search">
      <input
        type="text"
        placeholder="输入内容，回车搜索"
        v-model.lazy="game_SearchKey"
      />
    </div>
    <div class="main">
      <ul>
        <li
          v-show="flag"
          v-for="(v, i) in $store.state.gameContent.gameList.slice(start, end)"
          :key="i + 'v'"
        >
          <h3 v-show="v.name != 'null'">{{ v.name }}</h3>
          <hr v-show="v.name != 'null'" />
          <!-- <div class="main_header">
        </div> -->

          <div class="main_main">
            <div class="main_main_header">
              <span
                v-for="vType in v.type"
                v-show="vType != 'null'"
                :key="vType + 'vt'"
                >{{ vType }}</span
              >
            </div>
            <div class="main_main_main">
              <span v-show="v.activeKey != 'null'"
                >激活码:{{ v.activeKey }}</span
              >
            </div>
            <div class="main_main_main">
              <span v-show="v.extractKey != 'null'"
                >提取码:{{ v.extractKey }}</span
              >
            </div>
            <div class="main_footer">
              <a :href="v.url" target="_blank">点击下载</a>
            </div>
          </div>
        </li>
        <!-- 搜索显示的内容 -->
        <li v-show="searchFlag" v-for="(s, i) in filterList" :key="i + 's'">
          <!-- <div class="main_header"> -->
          <h3 v-show="s.name != 'null'">{{ s.name }}</h3>
          <hr v-show="s.name != 'null'" />
          <!-- </div> -->

          <div class="main_main">
            <div class="main_main_header">
              <span
                v-for="sType in s.type"
                v-show="sType != 'null'"
                :key="sType + 'st'"
                >{{ sType }}</span
              >
            </div>
            <div class="main_main_main">
              <span v-show="s.activeKey != 'null'"
                >激活码:{{ s.activeKey }}</span
              >
            </div>
            <div class="main_main_main">
              <span v-show="s.extractKey != 'null'"
                >提取码:{{ s.extractKey }}</span
              >
            </div>
            <div class="main_footer">
              <a :href="s.url" target="_blank">点击下载</a>
            </div>
          </div>
        </li>
      </ul>
      <div class="moreContent" v-show="hiddenBtn_more">
        <a>加载中...</a>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "gameContent",
  data() {
    return {
      flag: true,
      searchFlag: false,
      hiddenBtn_more: true,
      start: 0,
      end: 10,
      game_SearchKey: "",
      gameList: this.$store.state.gameContent.gameList,
      filterList: [],
    };
  },
  methods: {
    add() {
      // 储存this
      let _this = this;
      let timer = null;
      const container = document.querySelector(".container");
      const moreContent = document.querySelector(".moreContent");
      const moreContent_a = moreContent.querySelector('a')
      const containerHeight = container.clientHeight;
      // 获取距离
      // console.log(containerHeight);
      // console.log(top);

      function eventFun() {
        timer = null;
        if (timer !== null) {
          clearTimeout(timer);
        } else {
          timer = setTimeout(() => {
            let top = moreContent.getBoundingClientRect().top;
            if (containerHeight >= top) {
              // console.log("到底了");
              _this.end += 10;
              // console.log(_this);
              timer = !timer;
              // console.log(timer);
            }
          }, 700);
          if(_this.end >= _this.$store.state.gameContent.gameList.length){
            moreContent_a.innerText = '没有更多内容了'
          }
        }
      }
      eventFun();
    },

    // add() {
    //   if (this.end >= this.gameList.length - this.end) {
    //     this.end += this.end;
    //     this.hiddenBtn_more = false;
    //   } else {
    //     this.end += 20;
    //   }
    // },
  },
  watch: {
    game_SearchKey: {
      // 首次绑定是否执行 handler
      // immediate:true,
      handler(val) {
        // 输入框输入时替换列表
        if (this.game_SearchKey != "") {
          this.flag = false;
          this.searchFlag = true;
          this.hiddenBtn_more = false;

          // 过滤数组赋值给新数组
          this.filterList = this.$store.state.gameContent.gameList.filter(
            (item) => {
              // 判断是否在数组中存在|| item.name.toLowerCase().indexOf(val) !== -1
              // return item.name.indexOf(val) !== -1
              let nameUpper = item.name.toUpperCase();
              let nameLower = item.name.toLowerCase();
              if (
                nameUpper.includes(val) ||
                nameLower.includes(val) ||
                item.activeKey.includes(val) ||
                item.extractKey.includes(val)
              ) {
                return item;
                // console.log(item);
              }
            }
          );
        } else {
          this.flag = true;
          this.searchFlag = false;
          this.hiddenBtn_more = true;
        }
      },
    },
  },
};
</script>

<style scoped lang="less">
</style>